import React, { useEffect, useState } from 'react'
import { Row, Col, Button, Space } from 'antd'
import Search from 'antd/es/input/Search'
import { ShoppingCartOutlined } from "@ant-design/icons"
import Logo from "../Assets/logo.png"
import { Link } from 'react-router-dom'
import Login from '../Pages/Login'


const Header = () => {
    const [isModalOpen,setIsModalOpen] = useState(false)
    
    const handleClick=()=>{
        setIsModalOpen(true)
    }

    return (
        <Row style={{ display: "flex", alignItems: "center", justifyContent: "center", padding: "2rem 2.5rem" }}>
            <Col span={10} style={{ fontSize: "1.6rem", fontWeight: "600", display: "flex", alignItems: "center" }}>
            <img src={Logo} alt="amit-labs-logo" width="200px" />
            </Col>
            <Col style={{ display: "flex", alignItems: "center" }}>
                <Space size={"large"}>
                    <Search placeholder="Search your Product" enterButton style={{ width: "600px", background: "grey" }} />

                    <Button onClick={()=>handleClick()} style={{ background: "linear-gradient(90deg, rgba(121,79,153,1) 22%, rgba(150,79,153,1) 58%, rgba(97,79,153,1) 90%)", color: "white" }}>Login</Button>
                    <Login isModalOpen={isModalOpen} handleClick={handleClick} setIsModalOpen={setIsModalOpen}/>
                    <Link to={"/cart"} style={{color:'black'}}>
                    <Button style={{
                        color: "linear-gradient(90deg, rgba(121,79,153,1) 22%, rgba(150,79,153,1) 58%, rgba(97,79,153,1) 90%)", background: "#EFEFEF", fontWeight:
                            "500"
                    }}><ShoppingCartOutlined /> Cart</Button>
                    </Link>
                </Space>

            </Col>
        </Row>
    )
}

export default Header