import { LeftOutlined, RightOutlined } from '@ant-design/icons';
import { Col, Row, Image, Carousel, Card } from 'antd';
const { Meta } = Card;


const style = {
    color: '#fff',
    lineHeight: '200px',
    color: "#000"
    // paddingTop: '33px'
};

const CardCarasoul = ({ content = [] }) => (

    <>
        <div >
            <Row
                gutter={{
                    xs: 8,
                    sm: 16,
                    md: 24,
                    lg: 32,
                }}
            >
                {content.map((item, key) => (
                    <Col className="gutter-row" key={key} style={{margin:"0 2rem 0 0"}}>
                        <div style={style}>
                            <Image
                                src={item.img}
                                style={{ width: "150px",margin:"0px",objectFit:"contain",height:"150px" }}
                            />
                        </div>
                    </Col>
                ))}

            </Row>
        </div>
    </>
);
export default CardCarasoul;