import { LeftOutlined, RightOutlined } from '@ant-design/icons';
import { Card, Carousel } from 'antd'
import Meta from 'antd/es/card/Meta';
import React from 'react'


const Carasoul = ({ width = "", height = "", content = []}) => {
    const contentStyle = {
        height: height,
        width: width,
        color: '#fff',
        lineHeight: '160px',
        textAlign: 'center',
        background: '#364d79',
        margin: "0"
    };
    return (
        <Carousel autoplay arrows nextArrow={<RightOutlined />} prevArrow={<LeftOutlined />} >
            {content.map((item, i) => (
                <div key={i} >
                    <img src={item} alt="img" style={contentStyle} />
                </div>
            ))}
        </Carousel>
    )
}

export default Carasoul