import React from "react";
import { Divider, Avatar, Typography, } from 'antd';
import { Link } from 'react-router-dom'
import {
    InstagramOutlined,
    FacebookOutlined,
    UserOutlined,
    ShoppingCartOutlined,

} from "@ant-design/icons";
import { Layout, Menu, theme } from "antd";

const { Content, Sider } = Layout;

function Side() {
    const {
        token: { colorBgContainer }
    } = theme.useToken();
    return (
        <Sider style={{ background: colorBgContainer, }} width={320}>
            <div style={{ display: "flex", background: "linear-gradient(90deg, rgba(121,79,153,1) 22%, rgba(150,79,153,1) 58%, rgba(97,79,153,1) 90%)", color: "white", alignItems: "center" }}>
                <Avatar size={64} icon={<UserOutlined />} />
                <div>
                    <Typography.Title level={5} style={{ marginTop: 40, marginBottom: 0, marginLeft: 10, color: "white" }}>Hello</Typography.Title>
                    <Typography.Title level={3} style={{ marginTop: 0, marginBottom: 20, marginLeft: 10, color: "white" }}>Amit Lab Equipment</Typography.Title>
                </div>
            </div>
            <Menu
                mode="inline"
                defaultSelectedKeys={["1"]}
                defaultOpenKeys={["sub1"]}
                // style={{ height: "100%" }}
                items={[
                    {
                        key: "my-order", label: <h1> <ShoppingCartOutlined />             My Orders</h1>, children: [{
                            key: "option1", label: "Option-1"
                        }]
                    },
                    { label: <Divider /> },
                    {
                        key: "account-setting", label: <h1> <UserOutlined />             Account Setting</h1>, children: [{
                            key: "profile-information", label: "Profile Information"
                        },
                        { key: "manage-address", label: "Manage Address" }
                        ]
                    },
                    { label: <Divider /> },
                    {
                        key: "my-stuff", label: <h1> <ShoppingCartOutlined />              My Stuff</h1>, children: [{
                            key: "my-coupon", label: "My Coupon"
                        },
                        { key: "all-notification", label: "All Notification" },
                        { key: "my-wishes", label: "My Wishes" },
                        ]
                    },
                    { label: <Divider /> },
                    { key: "wish-list", label: <h1><ShoppingCartOutlined />          Logout</h1> },
                ]}
            />
        </Sider>
    )
}

export default Side;
