import {
    Button,
    Checkbox,
    Form,
    Input,
    Modal,
    Row,
    Col,
    Image,
    Typography,
    Space,
  } from "antd";
import { useEffect, useState } from "react";
  let background =
    "transparent linear-gradient(268deg, #3E3185 0%, #774F9A 26%, #6B2F85 74%, #393185 100%) 0% 0% no-repeat padding-box";
  const onFinish = (values) => {
    console.log("Success:", values);
  };
  const onFinishFailed = (errorInfo) => {
    console.log("Failed:", errorInfo);
  };
  const Login = ({isModalOpen,setIsModalOpen}) => {

    const handleCancle =()=>{
        setIsModalOpen(false)
    }

    return(

    <Modal open={isModalOpen} width={700}  onCancel={()=>handleCancle()}  footer={null}>
      <Row align={"top"} justify={"center"} gutter={[24, 8]}>
        <Col span={10}>
          <div
            style={{
              background: background,
              height: "400px",
              display: "block",
              padding: "23px",
              color: "white",
            }}
          >
            <h1 style={{ margin: "0px", paddingBottom: "23px" }}>Login</h1>
  
            <h5 style={{ margin: "0px", fontSize: "large" }}>
              Get access to your
            </h5>
  
            <h5 style={{ margin: "0px", fontSize: "x-large" }}>Order Wishlist</h5>
            <h5
              style={{ margin: "0px", fontSize: "large", paddingBottom: "25px" }}
            >
              and Recommendation
            </h5>
  
            <Image src="Assets/image1.PNG" preview={false}></Image>
          </div>
        </Col>
        <Col span={14}>
          <div style={{ display: "block", marginTop: "33px" }}>
            <Input
              size="large"
              placeholder="Email/Phone Number"
              style={{ marginBottom: "33px" }}
            />
            <Button
              style={{ marginBottom: "220px", background: background }}
              type="primary"
              block
              size="large"
            >
              Request OTP
            </Button>
  
            <div style={{ display: "flex", justifyContent: "center" }}>
              <Typography.Text>New to Amit Lab?</Typography.Text>
              <Typography.Text strong>Create Account?</Typography.Text>
            </div>
          </div>
        </Col>
      </Row>
    </Modal>
  )};
  export default Login;
  